/* Acquire.Busin.US CSS */

/* Importing fonts */
@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@400;500;600;700&family=Inter:wght@400;500;600&display=swap');

:root {
  /* Brand Colors from design.md */
  --brand-primary: #5ABB86;
  --brand-secondary: #0D1117;
  --brand-accent: #19D7C3;
  --brand-light-bg: #F8FAFB;
  --brand-dark-bg: #121212;
  --text-primary-light: #000000;
  --text-primary-dark: #FFFFFF;
  --text-secondary: #A1A1AA;
  --error-color: #FF4C4C;
  
  /* Typography */
  --font-heading: 'Space Grotesk', sans-serif;
  --font-body: 'Inter', sans-serif;
  
  /* Spacing */
  --spacing-xs: 4px;
  --spacing-sm: 8px;
  --spacing-md: 16px;
  --spacing-lg: 24px;
  --spacing-xl: 32px;
  --spacing-2xl: 48px;
  --spacing-3xl: 64px;
  
  /* Shadows */
  --shadow-sm: 0 4px 6px rgba(0, 0, 0, 0.1);
  --shadow-md: 0 10px 15px rgba(0, 0, 0, 0.1);
  --shadow-lg: 0 20px 25px rgba(0, 0, 0, 0.1);
  --glow-effect: 0 0 24px rgba(90, 187, 134, 0.1);
  
  /* Gradients */
  --hero-gradient: linear-gradient(120deg, #5ABB86, #19D7C3);
}

/* Base Styles */
.acquire-landing-page {
  font-family: var(--font-body);
  color: var(--text-primary-light);
  background-color: var(--brand-light-bg);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Header Styles */
.acquire-header {
  background-color: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(12px);
  border-bottom: 1px solid rgba(90, 187, 134, 0.25);
  position: sticky;
  top: 0;
  z-index: 100;
  box-shadow: var(--shadow-sm);
}

.acquire-header .logo h1 {
  font-family: var(--font-heading);
  color: var(--brand-primary);
  letter-spacing: -0.5px;
}

.acquire-header .navigation a {
  font-weight: 500;
  color: var(--brand-secondary);
  text-decoration: none;
  transition: all 0.3s ease;
}

.acquire-header .navigation a:hover {
  color: var(--brand-primary);
  transform: translateY(-2px);
}

/* Buttons */
.btn {
  display: inline-block;
  padding: 12px 24px;
  border-radius: 12px;
  font-weight: 600;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  text-decoration: none;
  box-shadow: var(--shadow-md);
  position: relative;
  overflow: hidden;
}

.btn:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.btn:active {
  transform: translateY(0);
}

.btn-primary {
  background: var(--hero-gradient);
  color: white;
}

.btn-primary:hover {
  box-shadow: 0 10px 20px rgba(90, 187, 134, 0.3);
}

.btn-secondary {
  background: transparent;
  color: var(--brand-primary);
  border: 2px solid var(--brand-primary);
}

.btn-secondary:hover {
  background: rgba(90, 187, 134, 0.1);
}

.btn-lg {
  padding: 16px 32px;
  font-size: 18px;
}

/* Hero Section */
.hero-section {
  background: var(--hero-gradient);
  color: white;
  padding: 80px 0;
  text-align: center;
}

.hero-section h1 {
  font-family: var(--font-heading);
  font-size: 64px;
  font-weight: 700;
  margin-bottom: 24px;
  letter-spacing: -1px;
}

.hero-section p {
  font-size: 20px;
  max-width: 700px;
  margin: 0 auto 40px;
  opacity: 0.9;
}

/* Features Section */
.features-section {
  background-color: white;
}

.features-section h2 {
  font-family: var(--font-heading);
  color: var(--brand-secondary);
  font-size: 48px;
  font-weight: 600;
}

.feature-card {
  text-align: center;
  padding: 32px;
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(90, 187, 134, 0.25);
  backdrop-filter: blur(12px);
  box-shadow: var(--glow-effect);
  transition: all 0.3s ease;
}

.feature-card:hover {
  transform: scale(1.05);
  border-color: var(--brand-accent);
}

.feature-card h3 {
  font-family: var(--font-heading);
  font-size: 24px;
  font-weight: 600;
  color: var(--brand-secondary);
  margin-bottom: 16px;
}

.feature-card p {
  color: var(--text-secondary);
  line-height: 1.6;
}

/* CTA Section */
.cta-section {
  background: var(--brand-dark-bg);
  color: white;
  text-align: center;
}

.cta-section h2 {
  font-family: var(--font-heading);
  font-size: 48px;
  font-weight: 600;
}

.cta-section p {
  font-size: 20px;
  max-width: 700px;
  margin: 0 auto 40px;
  opacity: 0.9;
}

/* Footer */
.acquire-footer {
  background-color: var(--brand-secondary);
  color: white;
}

.acquire-footer h3,
.acquire-footer h4 {
  font-family: var(--font-heading);
  color: white;
}

.acquire-footer a {
  color: var(--text-secondary);
  text-decoration: none;
  transition: all 0.3s ease;
}

.acquire-footer a:hover {
  color: var(--brand-primary);
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-section h1 {
    font-size: 40px;
  }
  
  .hero-section p {
    font-size: 18px;
  }
  
  .features-section h2,
  .cta-section h2 {
    font-size: 32px;
  }
  
  .feature-card {
    padding: 24px;
  }
  
  .acquire-header .navigation {
    display: none;
  }
  
  .btn {
    width: 100%;
    margin-bottom: 12px;
  }
  
  .cta-buttons .btn {
    margin-right: 0 !important;
  }
}